.about{
  color: red;
}

.aboutTransition-appear{
  opacity: 0;
}

.aboutTransition-appear.aboutTransition-appear-active{
  opacity: 1;
  color: red;
  -webkit-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}



